export default {
	data() {
		return {
			token : '',
			customerList:{},
			empty: false
		}
	},
	onLoad() {
		this.token = this.getGlobalAccessToken()
		if(this.token != null){
			uni.showLoading({
			    title: '加载中'
			});
			uni.request({
				url: this.serverUrl + 'customer-list',
				header: {
					"Authorization": this.token,
					"Accept":'application/json'
				},
				method: "GET",
				success: (res) => {
					uni.hideLoading();
					if(res.data.code == 200){
						this.customerList = res.data.data;
						this.empty = res.data.data.length ? false : true;
					}else if(res.data.code == 429){
						uni.showToast({
							title: res.data.message,
							image: "../../static/icons/warning.png"
						})
					}else{
						uni.showModal({
						    title: '未登录',
						    content: '您未登录，需要登录后才能继续',
							showCancel: false,
						    success: (res) => {
						        if (res.confirm) {
									uni.reLaunch({
									    url: '../registLogin/registLogin'
									});
						        }
						    }
						});
					}
				}
			});
		}
	},
	
	methods: {
		search(e){
			let text = e.detail.value ? e.detail.value :' ';
			uni.showLoading({
			    title: '加载中'
			});
			uni.request({
				url: this.serverUrl + 'customer-list?search=' + text,
				header: {
					"Authorization": this.token,
					"Accept":'application/json'
				},
				method: "GET",
				success: (res) => {
					uni.hideLoading();
					if(res.data.code == 200){
						this.customerList = res.data.data;
						this.empty = res.data.data.length ? false : true;
					}else if(res.data.code == 429){
						uni.showToast({
							title: res.data.message,
							image: "../../static/icons/warning.png"
						})
					}else{
						uni.showModal({
						    title: '未登录',
						    content: '您未登录，需要登录后才能继续',
							showCancel: false,
						    success: (res) => {
						        if (res.confirm) {
									uni.reLaunch({
									    url: '../registLogin/registLogin'
									});
						        }
						    }
						});
					}
				}
			});
		}
	}
}
